import React, { useState } from "react";
import styles from "../registration-form/registration-form.module.scss";
import {
  Button,
  EmailInput,
  Input,
  PasswordInput,
} from "@ya.praktikum/react-developer-burger-ui-components";
import { Link, Navigate } from "react-router-dom";
import { useRegistrationMutation } from "../../store/api/burgers.api";
import { toast } from "react-toastify";

const formState = {
  name: "",
  email: "",
  password: "",
};

function RegistrationForm() {
  const [formValues, setFormValue] = useState(formState);
  const { name, email, password } = formValues;
  const [registration, { isLoading }] = useRegistrationMutation();

  const handleChange = (e) => {
    setFormValue({ ...formValues, [e.target.name]: e.target.value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (name && email && password) {
      try {
        registration(formValues);
        return <Navigate to="/" />;
      } catch (error) {
        toast.error(error);
      }
    } else {
      toast.error("Заполните все поля");
    }
  };

  // useEffect(() => {
  //   if (isSuccess) {
  //     console.log("isSuccess", data);
  //   }
  // }, [isSuccess, data]);
  //
  // useEffect(() => {
  //   return () => {
  //     console.log("isError", isError, error);
  //   };
  // }, [isError, error]);

  return (
    <form className={styles.form} onSubmit={handleSubmit}>
      <Input
        name={"name"}
        placeholder={"Имя"}
        onChange={(e) => handleChange(e)}
        value={name}
        extraClass="mb-6"
      />
      <EmailInput
        name={"email"}
        placeholder={"E-mail"}
        onChange={(e) => handleChange(e)}
        value={email}
        extraClass="mb-6"
      />
      <PasswordInput
        name={"password"}
        onChange={(e) => handleChange(e)}
        value={password}
        error={false}
        extraClass="mb-6"
      />
      <Button
        htmlType="submit"
        type="primary"
        size="medium"
        extraClass="mb-20"
        disabled={isLoading}
      >
        Зарегистрироваться
      </Button>
      {/*{isError && (*/}
      {/*  <p className="text text_type_main-default text_color_inactive mb-4">*/}
      {/*    {error}*/}
      {/*  </p>*/}
      {/*)}*/}
      <p className="text text_type_main-default text_color_inactive mb-4">
        Уже зарегистрированы?{" "}
        <Link to={"/login"} className={styles.link}>
          Войти
        </Link>
      </p>
    </form>
  );
}

export default RegistrationForm;
